#wrapper {
  overflow-x: hidden;
}

#sidebar-wrapper {
  min-height: 100vh;
  margin-left: -15rem;
  -webkit-transition: margin 0.25s ease-out;
  -moz-transition: margin 0.25s ease-out;
  -o-transition: margin 0.25s ease-out;
  transition: margin 0.25s ease-out;
  background: rgb(51, 91, 243);
  background: linear-gradient(
    0deg,
    rgba(51, 91, 243, 1) 51%,
    rgba(40, 115, 245, 1) 78%,
    rgba(31, 138, 248, 1) 93%
  );
  color: white;
}

#sidebar-wrapper .sidebar-heading {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#sidebar-wrapper .list-group {
  width: 15rem;
  background-color: transparent;
 /*  background: rgb(51, 91, 243);
  background: linear-gradient(
    0deg,
    rgba(51, 91, 243, 1) 51%,
    rgba(40, 115, 245, 1) 78%,
    rgba(31, 138, 248, 1) 93%
  ); */
  color: white;
}

#page-content-wrapper {
  min-width: 100vw;
}

#wrapper.toggled #sidebar-wrapper {
  margin-left: -15rem;
}

#sidebar-wrapper {
  margin-left: 0;
}


@media (min-width: 768px) {
  #sidebar-wrapper {
    margin-left: 0;
  }

  #page-content-wrapper {
    min-width: 0;
    width: 100%;
  }

  #wrapper.toggled #sidebar-wrapper {
    margin-left: -15rem;
  }
}

