.main-bg {
  margin: 0;
  margin-top: auto;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background: rgb(30, 30, 47);
  background: linear-gradient(
    0deg,
    rgba(30, 30, 47, 1) 0%,
    rgba(41, 41, 65, 1) 33%,
    rgba(60, 60, 95, 1) 75%,
    rgba(108, 108, 170, 1) 90%,
    rgb(122, 122, 190) 100%
  );
}

/* Text Align */
.text-c {
  text-align: center;
}
.text-l {
  text-align: left;
}
.text-r {
  text-align: right;
}
.text-j {
  text-align: justify;
}

/* Text Color */
.text-whitesmoke {
  color: whitesmoke;
}
.text-darkyellow {
  color:  rgb(143, 162, 241);
  
}

.text-darkyellow:hover{
    color:rgb(51, 91, 243);
}

/* Lines */

.line-b {
  border-bottom: 1px solid #ffeb3b !important;
}

/* Buttons */

.form-button {
    background: rgb(51, 91, 243);
    background: linear-gradient(
      0deg,
      rgba(51, 91, 243, 1) 51%,
      rgba(40, 115, 245, 1) 78%,
      rgba(31, 138, 248, 1) 93%
    );
  border-color: rgb(51, 91, 243);
  border-radius: 25px;
  color: #e6e6e6;
}
.form-button:hover,
.form-button:focus,
.form-button:active,
.form-button.active,
.form-button:active:focus,
.form-button:active:hover,
.form-button.active:hover,
.form-button.active:focus {
  background-color: rgb(51, 91, 243);
  border-color: rgb(51, 91, 243);
  color: #e6e6e6;
}
.button-l {
  width: 50% !important;
}

